@import url(https://fonts.googleapis.com/css2?family=Open+Sans&display=swap);
@keyframes typing{from{width:0}to{width:100%}}@keyframes blink-caret{from,to{border-color:transparent}50%{border-color:#BA7D8A}}.typewriter{overflow:hidden;border-right:.15em solid transparent;white-space:nowrap;margin:0 auto;animation:typing 2s steps(40, end),blink-caret 0.75s step-end 6}body{font-family:'Open Sans', 'Helvetica Neue';background-color:white}.attribution{text-align:right;position:absolute;margin-right:5px;font-size:12px;bottom:5px;right:5px}.vertical-div{height:100px;line-height:100px}.vertical-span{display:inline-block;line-height:normal}

.menu{margin:auto;text-align:center}#photo{float:right;width:24rem;border-radius:50%;margin:18vh;margin-top:17vh;-webkit-filter:drop-shadow(0px 4px 4px rgba(0,0,0,0.25));filter:drop-shadow(0px 4px 4px rgba(0,0,0,0.25))}.left{float:left;margin-top:24vh;margin-left:26vh;width:60.91vh}.home h1{font-size:50px;text-align:left}.home h2{color:#011640;margin-top:15px;text-align:left}.home h3{margin-top:20px;margin-bottom:20px}.home .icon{width:9%;margin-right:15px}.home .icon:hover{margin-top:-5px}.home .icon-div{margin-top:10px;float:right}.home .btn-default{color:white;background-color:#BA7D8A;font-weight:bold;font-size:14px;padding:9px;border-radius:70%;-webkit-filter:drop-shadow(0px 4px 4px rgba(0,0,0,0.25));filter:drop-shadow(0px 4px 4px rgba(0,0,0,0.25))}.home .btn:hover{margin-top:-5px}





